.popup-list-box-container,
.popup-list-box{
    display: block;
    width: 100%;
    position: relative;
}

.popup-list-box{
    border: 2px solid #000;
    border-radius: 5px;
}

.popup-list-box-container > label.pop-up-label{
    align-self: start;
    background-image: linear-gradient(to top, #fff 0 calc(50% - 2px), #fff calc(50% - 2px));
    font-size: 0.6rem;
    padding-left: 0.5em;
    padding-right: 0.5em;
    transform: translate(0.5rem,-50%);
    position: absolute;
    color: #000;
    font-weight: 700;
    letter-spacing: 1.2px;
    z-index: 2;
    border-radius: 5px;
    outline: 2px solid #000;
    white-space: nowrap;
}

.popup-list-box-container label.required::after{
    content: '*';
    font-family: 'Courier New', Courier, monospace, 'Franklin Gothic Medium' , 'Times New Roman', Times, serif, Arial, Helvetica, sans-serif, Georgia ;
    color: red;;
}

.popup-list-box-container label span{
    font-size: .6rem;
    margin-left: 5px;
    font-weight: 400;
}

.popup-list-box {
    margin: 0 auto;
    font-size: 1rem;
    color: #60666d;
}

